.header {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px 5px 10px;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;
  background-color: #ffffffcb;
  backdrop-filter: blur(10px);
}

.logo {
  width: 135px;
  padding: 0;
}

.header .buttons button {
  margin: 0 3px;
}

.buttons button:hover {
  transform: scale(1.2);
}

.logo img {
  width: 100%;
  height: 100%;
}
